/* ------------------- COMMON ----------------------- */
.fashion_title{
    font-weight: 600;
    font-size: 18px;
    color: var(--md-color-secondary);
}

.fashion_padding{
    padding: 0.5rem;
}

.fashion_center{
    text-align: center;
}

.fashion_border-bottom{
    border-bottom: 1px solid var(--md-bg-ccc);
}


/* ------------------------------------------ */
.FashionHCommon{
    padding: 0.5rem;
    background-color: var(--fashion-hover);
    box-shadow: 0 0 6px 1px var(--md-bg-ccc);
}

// ..
.FashionHCommon_left{
    width: 10%;
}
// ..
.FashionHCommon_center{
    width: 70%;
}
// ..
.FashionHCommon_right{
    width: 20%;
}

//
@media (max-width: 600px){
    .FashionHCommon{
        position: relative;
    }

    .FashionHCommon_row{
        display: block;
    }
    // .
    .FashionHCommon_left{
        width: fit-content;
        margin-bottom: 0.5rem;
    }
    // ..
    .FashionHCommon_center{
        width: 100%;
    }
    // ..
    .FashionHCommon_right{
        position: absolute;
        right: 0.5rem;
        top: 1rem;

        width: fit-content;
    }
}